import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';

// @mui
import { styled, alpha } from '@mui/material/styles';
import {
  Toolbar, OutlinedInput,
  InputAdornment,
  Typography,
  Tooltip,
  IconButton,
  Box,
  Stack
} from '@mui/material';
// component
import Iconify from 'components/iconify';
import { ManageSearch } from '@mui/icons-material';
import FormatAlignJustifyIcon from '@mui/icons-material/FormatAlignJustify';
import GridViewIcon from '@mui/icons-material/GridView';
// ----------------------------------------------------------------------

const StyledRoot = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3),
}));

const StyledSearch = styled(OutlinedInput)(({ theme }) => ({
  width: 240,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter,
  }),
  '&.Mui-focused': {
    width: 320,
    boxShadow: theme.shadows[6],
  },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${alpha(theme.palette.grey[500], 0.32)} !important`,
  },
}));

// ----------------------------------------------------------------------

CustomerListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
};

export default function CustomerListToolbar({ numSelected, filterName, onFilterName }) {
  const navigate = useNavigate();
  return (
    <StyledRoot
      sx={{
        ...(numSelected > 0 && {
          color: 'text.default',
          bgcolor: 'background.default',
        }),
      }}
    >
      <Box sx={{ flexGrow: 1 }}>
        {numSelected > 0 ? (
          <Stack direction={'row'} spacing={2} justifyContent={'flex-start'} alignItems={'center'}>
            <Typography component="div" variant="subtitle1">
              {numSelected} selected
            </Typography>
            <Tooltip title="Delete">
              <IconButton>
                <Iconify icon="eva:trash-2-fill" />
              </IconButton>
            </Tooltip>
          </Stack>
        ) : (
          <StyledSearch
            value={filterName}
            onChange={onFilterName}
            placeholder="Search user..."
            startAdornment={
              <InputAdornment position="start">
                {/* <Iconify icon="eva:search-fill" sx={{ color: 'text.disabled', width: 20, height: 20 }} /> */}
                <ManageSearch sx={{ color: 'text.disabled', width: 28, height: 28 }} />
              </InputAdornment>
            }
          />
        )}
      </Box>
      <Tooltip title="Delete">
        <IconButton onClick={() => navigate('/pages/customers')}>
          <FormatAlignJustifyIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Delete">
        <IconButton onClick={() => navigate('/pages/customers/card')}>
          <GridViewIcon />
        </IconButton>
      </Tooltip>
      {/* {numSelected > 0 ? (
        <></>
      ) : (
        <Tooltip title="Filter list">
          <IconButton>
            <Iconify icon="ic:round-filter-list" />
          </IconButton>
        </Tooltip >
      )
      } */}
    </StyledRoot >
  );
}
